import React, { Component } from "react";
import Modal from "../MyModal/index";
import * as noteApi from "../../apis/note";
import * as myToast from "../../../Common/helper/toastHelper";
import Cookies from "universal-cookie";

const cookie = new Cookies();

export default class DeleteNote extends Component {
  async saveChanges() {
    var token = cookie.get("token");
    var refreshtoken = cookie.get("refreshtoken");
    await noteApi
      .deleteNote(this.props.bill.maphieunhap, token, refreshtoken)
      .then(() => {
        myToast.toastSucces("Xo thnh cng");
        this.props.handleSave();
      })
      .catch((error) => {
        myToast.toastError("Xo tht bi");
        console.log(error);
      });
  }
  render() {
    return (
      <Modal
        handleClose={this.props.handleClose}
        handleSave={() => {
          this.saveChanges();
        }}
        title={"Xo phiu nhp"}
      >
        <p>Bn c chc chn mun xo phiu nhp sn phm ny</p>
      </Modal>
    );
  }
}
